import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AisleService } from 'src/app/service/aisle.service';

@Component({
  selector: 'app-aisle-view',
  templateUrl: './aisle-view.component.html'
})
export class AisleViewComponent implements OnInit {

  viewId: any;
  objectArray: any;
  popup: Window;

  constructor(
    private activatedRoute: ActivatedRoute,
    private aisleservice: AisleService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.viewId = this.activatedRoute.snapshot.paramMap.get('id');
    }
  }
  ngOnInit(): void {
    this.aisleservice.getAisleById(this.viewId).subscribe((response) => {
      if (response.success) {
        this.objectArray = response.data;

      }
    });
  }

  VoucherSourcetoPrint() {
    return (
      `
            <html>
            <head>
              <script>
                function step1() {
                  setTimeout('step2()', 10);
                }
                function step2() {
                  window.print(); window.close()
                }
              </script>
            </head>

            <body onload='step1()'>
              <h3>Aisle Name:- ${this.objectArray?.aisle_name}</h3>
              <h3>Aisle Code:- ${this.objectArray?.aisle_code}</h3>
              <img src='${this.objectArray?.aisle_bar_code_image}' />
            </body>
            </html>
      `
    );
  }
  VoucherPrint() {
    // let pwa = window.open();
    // pwa.document.open();
    // pwa.document.write(this.VoucherSourcetoPrint());
    // pwa.document.close();
    window.print();
  }


}

