import { Component, OnInit } from '@angular/core';
import { PaginationService } from 'src/app/service/pagination.service';
import { TextField, ActionField } from 'src/app/common-module/table-list/list-field-type';
import { AisleService } from 'src/app/service/aisle.service';

@Component({
  selector: 'app-aisle-list',
  templateUrl: './aisle-list.component.html'
})
export class AisleListComponent implements OnInit {

  PageTitle = "Manage Aisle";

  action: any;
  loadform = false;
  loadingState = true;
  objectArray: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  permissionObject: any = null;
  showPagination: boolean = false;
  //For Dynamic List
  headerData: Array<any> = [];

  constructor(
    private paginationService: PaginationService,
    private aisleService: AisleService,
  ) { }

  ngOnInit() {
    // this.dataService.permission.subscribe((perms) => {
    //   this.permissionObject = perms['self'];
    // });
    this.loadform = false;
    this.setHeaderData();
    this.getObjects();
  }
  setHeaderData() {
    this.headerData.push(
      new TextField({
        label: "Aisle Name",
        key: "aisle_name",
      }),
      // new TextViewUrlField({
      //   label: "Order #",
      //   key: "po_no",
      //   viewUrl: {
      //     path: '/inbound/order/view/',
      //     id: 'po_id'
      //   }
      // }),
      new TextField({
        label: "Aisle Code",
        key: "aisle_code",
      }),
      new TextField({
        label: "Bar Code No",
        key: "aisle_bar_code",
      }),

    );
    let canEdit = true;
    let canView = true;
    let canDelete = true;
    let actions = {};
    if (canEdit) {
      actions['edit'] = {
        path: '/manage/aisle/edit/',
        id: 'aisle_id'
      }
    }
    if (canView) {
      actions['view'] = {
        path: '/manage/aisle/view/',
        id: 'aisle_id'
      }
    }
    if (canDelete) {
      actions['delete'] = {
        path: 'aisle/',
        id: 'aisle_id'
      }
    }
    let actionColom = new ActionField(actions);
    this.headerData.push(
      actionColom
    );
  }

  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.aisleService.getAisleList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success) {
        this.objectArray = response.data.list;
        this.showPagination = true;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }
  onReloadEvent() {
    this.getObjects();
  }
}




