import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { SharedModule } from 'src/app/shared/shared.module';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { AisleService } from 'src/app/service/aisle.service';
import { AddAisleComponent } from './add-aisle/add-aisle.component';
import { AisleListComponent } from './aisle-list/aisle-list.component';
import { AisleViewComponent } from './aisle-view/aisle-view.component';
import { NgSelectModule } from '@ng-select/ng-select';


const routes: Routes = [
  {
    path: "",
    component: AisleListComponent,
    data: { title: 'aisle_list' }
  },
  {
    path: "add",
    component: AddAisleComponent,
    data: { title: 'add_aisle' }
  },
  {
    path: 'edit/:id',
    component: AddAisleComponent,
    data: { title: 'edit_aisle' }
  },
  {
    path: 'view/:id',
    component: AisleViewComponent,
    data: { title: 'view_aisle' }
  },
]

@NgModule({
  declarations: [AisleListComponent, AddAisleComponent, AisleViewComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    TableListModule,
    SearchModule,
    BtnAddModule,
    FormsModule,
    ReactiveFormsModule,
    FormValidationModule,
    NgSelectModule,
    SharedModule,
    PaginationModule
  ], providers: [AisleService]
})
export class AisleModule { }
